<template>
    <div class="app-container" v-loading="containerLoad">
        <el-button @click="addDialogShow">添加关联</el-button>
        <el-button @click="registerFormVisible=true">电信注册</el-button>
        <el-table :data="tableData" v-loading="isLoad" border style="width: 100%">
            <el-table-column align="center" prop="mid" label="厂商编号" width="140">
            </el-table-column>
            <el-table-column align="center" prop="mname" label="厂商名称" width="300">
            </el-table-column>
            <el-table-column align="center" prop="ctId" label="电信编号" width="150">
            </el-table-column>
            <el-table-column align="center" prop="ctName" label="电信名称">
            </el-table-column>
        </el-table>
        <div style="text-align:center;margin: 20px auto;">
            <el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
            </el-pagination>
        </div>
        <el-dialog title="添加关联" :visible.sync="addFormVisible">
            <el-form :model="form">
                <el-form-item label="厂商" label-width="300">
                    <el-select v-model="form.mid" filterable placeholder="请选择" style="width: 100%;">
                        <el-option v-for="m in mList" :key="m.mid" :label="m.mname" :value="m.mid">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="电信" label-width="300">
                    <el-select v-model="form.ctId" filterable placeholder="请选择" style="width: 100%;">
                        <el-option v-for="ct in ctList" :key="ct.ctId" :label="ct.ctName" :value="ct.ctId">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addAction">确 定</el-button>
            </div>
        </el-dialog>
    <el-dialog title="电信注册" :visible.sync="registerFormVisible">
  <el-form :model="rform"  ref='rform':rules="rules">
    <el-form-item label="电信名称" label-width="200" prop="name">
      <el-input v-model="rform.name" auto-complete="off"></el-input>
    </el-form-item>
        <el-form-item label="电信地址" label-width="200" prop="address">
      <el-input v-model="rform.address" auto-complete="off"></el-input>
    </el-form-item >
        <el-form-item label="联系人" label-width="200" prop="contacts">
      <el-input v-model="rform.contacts" auto-complete="off"></el-input>
    </el-form-item >
        <el-form-item label="联系电话" label-width="200" prop="contactPhone">
      <el-input v-model="rform.contactPhone" auto-complete="off"></el-input>
    </el-form-item >
        <el-form-item label="账号" label-width="200" prop="account">
      <el-input v-model="rform.account" auto-complete="off"></el-input>
    </el-form-item >
        <el-form-item label="密码" label-width="200" prop="password">
      <el-input v-model="rform.password" auto-complete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="registerFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="registerAction">确 定</el-button>
  </div>
</el-dialog>
    </div>
</template>


<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import {
    getListManufacturerGeneral,
    getListCTInfoGeneral
} from 'api/admin/usual';
import {
    listCTAndManufacturer,
    saveCTManufacturer,
     registerCT
} from 'api/admin/roleManager'
import { backCN } from 'utils/index';
export default {
    data() {
        return {
            tableData: [],
            tableCount: 0,
            currentPage: 1,
            form: {
                mid: '',
                ctId: ''
            },
            rform:{
                name:'',
                address:'',
                contacts:'',
                contactPhone:'',
                account:'',
                password:'',
            },
            rules:{
                 name:[{ required: true, message: '名称不能为空' }],
                address:[{ required: true, message: '地址不能为空' }],
                contacts:[{ required: true, message: '联系人不能为空' }],
                contactPhone:[{ required: true, message: '联系电话不能为空' }],
                account:[{ required: true, message: '账号不能为空' }],
                password:[{ required: true, message: '密码不能为空' }],
            },
            mList: [],
            ctList: [],
            addFormVisible: false,
            registerFormVisible: false,
            containerLoad: false,
            isLoad:true
        }
    },
    watch: {},
    mounted() {
        this.getListCTAndManufacturer(1, 10)
    },
    methods: {
        addDialogShow() {
            this.containerLoad = true
            getListManufacturerGeneral().then(res => {
                if (res.msg === 0) {
                    this.mList = res.mList
                    this.form.mid = res.mList[0].mid
                    getListCTInfoGeneral().then(res => {
                        if (res.msg === 0) {
                            this.containerLoad = false
                            this.ctList = res.ctList
                            this.form.ctId = res.ctList[0].ctId
                            this.addFormVisible = true
                        } else {
                            this.$message.error(backCN(res.code))
                            this.containerLoad = false
                        }
                    }).catch(() => {
                        this.containerLoad = false
                    })
                } else {
                    this.$message.error(backCN(res.code))
                    this.containerLoad = false
                }
            }).catch(() => {
                this.containerLoad = false
            })
        },
        addAction() {
            let data = {
                mid: this.form.mid,
                ctId: this.form.ctId
            }
            this.addFormVisible = false
            this.isLoad=true
            saveCTManufacturer(data).then(res => {
                res.msg === 0 ? this.$message.success("添加成功！") : this.$message.error(backCN(res.code))
                res.msg===0?this.getListCTAndManufacturer(1, 10):''
                res.msg!=0?this.isLoad=false:''
            }).catch(err=>{
                   this.isLoad=false;
                   this.$message.error(backCN(err.status))
               })
        },
        registerAction() {
        this.$refs["rform"].validate((valid) => {
          if (valid) {
                 let data={
                  ctInfo:JSON.stringify({
                      "name":this.rform.name,
                      "address":this.rform.address,
                      "contacts":this.rform.contacts,
                      "contactPhone":this.rform.contactPhone
                  }),
                  accountInfo:JSON.stringify({
                      "account":this.rform.account,
                      "password":this.rform.password
                  })
              }
              this.registerFormVisible=false
              this.isLoad=true
               registerCT(data).then(res=>{
                   res.msg===0?this.$message.success("注册成功！"):this.$message.error(backCN(res.code))
                  res.msg===0?this.getListCTAndManufacturer(1, 10):''
                res.msg!=0?this.isLoad=false:''
               }).catch(err=>{
                   this.isLoad=false;
                   this.$message.error(backCN(err.status))
               })
          } else {
            return false;
          }
        })
         
        },
        getListCTAndManufacturer(page, num) {
            this.isLoad=true
            listCTAndManufacturer({ page, num }).then(res => {
                this.isLoad=false
                console.log("res", res)
                if (res.msg == 0) {
                    this.tableData = res.list;
                    page === 1?
                        this.tableCount = res.count:''
                    
                } else {
                    this.tableData = [];
                    this.tableCount = 0;
                    res.msg!=1?this.$message.error(backCN(res.code)):''
                }
            }).catch(err=>{
                this.isLoad=false
              this.$message.error(backCN(err.status))
            })
        },
        pageChange(page) {
            this.getListCTAndManufacturer(page, 10)
        },
    }
};
</script>

<style scoped>
.app-container {
    background: #f5f5f5;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both
}

.box-card {
    width: 100%;
    min-width: 637px;
    margin-bottom: 20px;
}

.survey-container {}

.survey-item-wrap {
    padding: 5px 0;
}

.survey-item {
    border-right: 1px solid #e0e0e0;
}

.survey-item-wrap:last-child .survey-item {
    border: none;
}

.survey-item h3 {
    font-size: 13px;
    color: #666;
}

.survey-item p {
    color: #f88b00;
    font-size: 25px;
}

.content-rowspan div {
    padding-left: 13px;
    line-height: 46px;
    border-bottom: 1px solid #ECEDEE;
}

.extend .cell {
    padding: 0;
}

.flow-details {
    border-bottom: 1px solid #e8e8e8;
}

.flow-details:last-child {
    border: none;
}
</style>



