//角色管理
import fetch from 'utils/fetch';


//管理员获取关联信息
export function listCTAndManufacturer(data) {
    return fetch({
        url: 'recharge/manager/listCTAndManufacturer.do',
        method: 'get',
        data:data
    })

}

//管理员存储厂商与电信关联信息
export function saveCTManufacturer(data){
    return fetch({
        url:'recharge/manager/saveCTManufacturer.do',
        method:'post',
        data:data
    })
}

//电信注册
export function registerCT(data){
    return fetch ({
        url:'recharge/manager/registerCT.do',
        method:'post',
        data:data
    })
}



